import { useSearchParams } from 'react-router-dom';
import { CVData } from './ClassesCVData';
import { currenrCVData, setCurrentCVData_v2 } from './GlobalCVData';
import { exit } from 'process';
var _ = require('lodash');

function OpenCVdataFile(props: any) {

  const [searchParams] = useSearchParams();
  const filename = searchParams.get('filename');

  if (filename == null)
    return (<></>);

  async function getCVdata() {

    let url = 'https://johnpetersendenmark.github.io/JohnPetersenCV/static/css/' + filename
    let response;
    let newCVData: CVData;
    let OldCurrentCDData

    try {
      response = await fetch(url);
      console.log('Promise resolved with value: ' + response);
      if (!response.ok)
        if (response.status == 404) {
          console.error('not found: ' + response.status);
          return (<></>)
        }

      newCVData = await response.json() as CVData;
      OldCurrentCDData = currenrCVData;
      setCurrentCVData_v2(newCVData)
      if (!(_.isEqual(currenrCVData, OldCurrentCDData))) {
        props.SetCVdata(currenrCVData);
      }

    } catch (error) {
      console.error('Network error: ' + error);
    }





    //try {

    //   }
    // newCVData = await response.json() as CVData;
    //if (response.ok) {
    //  OldCurrentCDData = currenrCVData;
    //  setCurrentCVData_v2(newCVData)
    //}
    // else
    // return(<></>);

    // } catch (error) {
    // if (error instanceof SyntaxError) {
    //   // Unexpected token < in JSON
    //   console.log('There was a SyntaxError', error);
    // } else {
    //   console.log('There was an error', error);
    // }
    //}


    // if (!(_.isEqual(currenrCVData, OldCurrentCDData))) {
    //   props.SetCVdata(currenrCVData);
    // }
  }

  getCVdata();

  return (
    <> {filename}</>
  );

}


export default OpenCVdataFile;